/**
 * Typography
 *
 * @since 2.0.0
 */

body {
	font-family: var(--font-family-sans-serif);
	font-size: var(--font-size-regular);
	color: var(--color-white);
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: var(--font-family-heading);
	font-weight: 400;
}


a {

	&,
	&:visited {
		color: var(--color-white);
	}
}
